/* eslint-disable no-unused-vars */
/* eslint-disable camelcase */
import React, { useState, useEffect } from 'react';
import {
  Flex, Box, Image, Text,
} from 'rebass';
import { Button, Input } from '@liveperson/lp-react-components';
import { Link, Redirect, useLocation } from 'react-router-dom';
import Logo from './Logo';
import Shield from '../assets/shield.svg';

const LOGO_SIZE = 44;
const INPUT_SIZE = 44;
const INPUTS = [1, 2, 3, 4, 5];

// TOOD: clean up these styles and put them in the theme file where possible

// TODO: refactor this file into smaller components

const gridTemplateColumns = `${INPUT_SIZE}px ${INPUT_SIZE}px ${INPUT_SIZE}px ${INPUT_SIZE}px ${INPUT_SIZE}px`;

// import new library so that I can pass props
// TOOD: max lenght of input field

function OneTimePassword() {
  const [digits, setDigits] = useState(['', '', '', '', '']);
  const [errors, setErrors] = useState([false, false, false, false, false]);
  const [errorMessage, setErrorMessage] = useState('');
  const [isValidOTP, setIsValidOTP] = useState(false);
  const [resendSeconds, setResendSeconds] = useState(5);
  const [selectedDigit, setSelectedDigit] = useState(0);
  const [attempts] = useState(1);
  const location = useLocation();
  const creationId = location?.state?.creationId;

  // TODO: memoize these functions so that they don't get recreaed on render
  const startResendTimer = () => {
    let seconds = 5;
    const interval = setInterval(() => {
      if (seconds > 0) {
        seconds -= 1;
      } else {
        seconds = 0;
        clearInterval(interval);
      }
      setResendSeconds(seconds);
    }, 1000);
  };

  const isDigitErrored = (digit) => {
    const number = parseInt(digit, 10);
    return !(number >= 0 && number < 9);
  };

  const validateDigit = (index, value) => {
    setErrorMessage('');
    const digitErrors = [...errors];
    digitErrors[index] = isDigitErrored(value);
    setErrors(digitErrors);
  };

  const validateDigits = () => {
    setErrorMessage('');
    const digitErrors = digits.map(isDigitErrored);
    setErrors(digitErrors);
    return digitErrors.reduce((a, d) => !d && a, true);
  };

  const verifyOTP = async () => {
    if (validateDigits()) {
      await fetch(`${process.env.REACT_APP_ENDPOINT}/verify/otp`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Accept: 'application/json',
        },
        body: digits.join(''),
      }).then((response) => response.json())
        .then((result) => {
          setIsValidOTP(true);
          console.log('result: ', result);
        });
    } else {
      setErrorMessage('Code not filled out correctly');
    }
  };

  const resendOTP = async () => {
    startResendTimer();
    // Clear the current fields since we are generating a new code
    setDigits(['', '', '', '', '']);
    setErrors([false, false, false, false, false]);

    // await fetch(`${process.env.REACT_APP_ENDPOINT}/resend/otp`, {
    //   method: 'POST',
    //   headers: {
    //     'Content-Type': 'application/json',
    //     Accept: 'application/json',
    //   },
    //   body: 'accountId',
    // }).then((response) => response.json())
    //   .then((result) => {
    //     // TODO: set the OTP counter with the result returned from the api call
    //     // TODO: restart the OTP counter if the OTP counter is less than 3
    //     console.log('result: ', result);
    //   });
  };

  const handleNumberChange = (name, value, index) => {
    const newDigits = [...digits];
    newDigits[index] = value;
    setDigits(newDigits);
    validateDigit(index, value);
  };

  useEffect(() => {
    startResendTimer();
    return () => setIsValidOTP(false);
  }, []);

  if (isValidOTP) {
    return (
      <Redirect
        push
        to={{
          pathname: '/creating-account',
          state: { digits, creationId },
        }}
      />
    );
  }

  return (
    <Box
      backgroundColor="navy"
      sx={{
        height: '100%',
        flex: 1,
        overflow: 'auto',
        display: 'grid',
        color: 'white',
        gridTemplateRows: 'auto auto 1fr',
        gridTemplateAreas: "'logo' 'fields' 'line'",
        '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
          display: '-ms-grid',
          msGridRows: 'auto auto 1fr',
        },
      }}
    >
      <Box sx={{ gridArea: 'logo', msGridRow: '1' }} mb={3}>
        <Link to="/">
          <Logo isStatic />
        </Link>
      </Box>
      <Flex
        mb={7}
        alignItems="center"
        flexDirection="column"
        sx={{ gridArea: 'fields', msGridRow: '1' }}
      >
        <Flex
          justifyContent="center"
          alignItems="center"
          flexDirection="column"
          maxWidth={500}
          minWidth={320}
        >
          <Image
            src={Shield}
            alt="shield"
            height={LOGO_SIZE}
            width={LOGO_SIZE}
          />
          <Text
            sx={{
              gridArea: 'fields',
              msGridRow: '1',
              fontSize: 3,
              fontWeight: 700,
              lineHeight: '32px',
              letterSpacing: '0.008em',
              WebkitFontSmoothing: 'antialiased',
              textAlign: 'center',
            }}
            my={3}
            px={4}
          >
            Verify your email address
          </Text>
          <Text
            sx={{
              gridArea: 'fields',
              msGridRow: '1',
              fontSize: 1,
              lineHeight: '24px',
              letterSpacing: '0.01em',
              WebkitFontSmoothing: 'antialiased',
              textAlign: 'center',
            }}
            mb={5}
            px={4}
          >
            Please enter the OTP code sent to your email to continue
          </Text>
          <Flex
            flexDirection={['column', 'row']}
            alignItems={['center', 'flex-start']}
          >
            <Box
              sx={{
                display: 'grid',
                gridGap: 12,
                gridTemplateColumns,
              }}
              mb={[3, 0]}
            >
              {INPUTS.map((id, index) => (
                <Input
                  key={id}
                  isFullWidth
                  type="number"
                  height={INPUT_SIZE}
                  fontSize={2}
                  maxLength={1}
                  name={`input-${id}`}
                  value={digits[index]}
                  isErrored={errors[index]}
                  shouldFocusOnLoad={selectedDigit === index}
                  handleFocus={() => setSelectedDigit(index)}
                  handleChange={(name, value) => {
                    handleNumberChange(name, value, index);
                    if (!isDigitErrored(value)) {
                      setSelectedDigit((d) => d + 1);
                    }
                  }}
                  onKeyUp={(e) => (e.key === 'Enter') && verifyOTP()}
                />
              ))}
            </Box>
            <Button
              label="Verify OTP"
              px={4}
              ml={[0, 5]}
              fontSize={1}
              sx={{ borderRadius: 20, height: 40 }}
              onClick={verifyOTP}
            />
          </Flex>
          {errorMessage && (
          <Text sx={{
            color: '#F29295',
            fontSize: 0,
            lineHeight: '20px',
            letterSpacing: '0.01em',
            WebkitFontSmoothing: 'antialiased',
            alignSelf: 'flex-start',
            marginTop: 2,
          }}
          >
            {errorMessage}
          </Text>
          )}
        </Flex>
      </Flex>
      <Flex sx={{ gridArea: 'line' }} px={5} justifyContent="center">
        <Flex
          justifyContent={['initial', 'space-between']}
          flexDirection={['column', 'row']}
          alignItems={['center', 'flex-start']}
          sx={{ borderTop: '1px solid #6C7189' }}
          maxWidth={1020}
          flex={1}
        >
          <Flex fontSize={0} mt={5} sx={{ WebkitFontSmoothing: 'antialiased' }}>
            <Text fontWeight={600} mr={1}>Tip:</Text>
            Don&apos;t see the email in your inbox? Check your spam folder
          </Flex>
          {resendSeconds === 0 ? (
            <Text
              fontSize={0}
              mt={[6, 5]}
              sx={{
                WebkitFontSmoothing: 'antialiased',
                color: 'aqua',
                cursor: 'pointer',
                '&:hover': {
                  textDecoration: 'underline',
                },
              }}
              onClick={resendOTP}
            >
              {`Resend OTP ${resendSeconds === 0 ? `(attempt: ${attempts} of 3)` : `in ${resendSeconds} seconds ...`}`}
            </Text>
          ) : (
            <Text fontSize={0} mt={[6, 5]} sx={{ WebkitFontSmoothing: 'antialiased' }}>
              {`Resend OTP ${resendSeconds === 0 ? `(attempt ${attempts} of 3)` : `in ${resendSeconds} seconds ...`}`}
            </Text>
          )}
        </Flex>
      </Flex>
    </Box>
  );
}

export default OneTimePassword;
